.TodoItemLoading{
    background-color:gray;
    position:relative;
    display:flex;
    justify-content:center;
    align-items:center;
    margin-top:24px;
    box-shadow:0 5px 50px rgba(32,35,41,.15);
    animation: loading-skeleton infinite 3s;
}
.TodoItemLoading-p{
    margin:24px 0 24px 24px;
    width:calc(100% - 120px);
    font-size:18px;
    line-height:24px;
    font-weight:400;
}
.TodoItemLoading-p--complete
{
    text-decoration:line-through;
}
.IconLoading{
    cursor:pointer;
    display:flex;
    justify-content:center;
    align-items:center;
    height:48px;
    width:48px;
    font-size:24px;
    font-weight:bold;
}
.IconLoading-check
{position:absolute;
    left:12px;
}
.IconLoading-check--active
{color:#4caf50
}
.IconLoading-delete
{position:absolute;
    top:-24px;
    right:0
}
.IconLoading-delete:hover{
    color:red
}

/* Animations */
@keyframes loading-skeleton {
    0%, 100% {
      opacity: 100%;
    }
    50% {
      opacity: 0%;
    }
}