form{
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    border-color: white;
    border: 1px;
    width: 70%;
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    gap: 10px;

}
label{
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 26px;
}
textarea{
    background:#f9fbfc;
    border-radius:2px;
    border:2px solid #202329;
    margin:0 24px;
    height:97px;
    width:calc(100% - 25px);
    font-size:20px;
    text-align:center;
    font-family:'montserrat';
    font-weight:400;
    color:#1e1e1f;
    padding: 12px;
    box-shadow:0 5px 50px rgba(32,35,41,.25);

}
textarea::placeholder{
    color:#a5a5a5;
    font-family:montserrat;
    font-weight:400;
}
textarea:focus{
    outline-color:#61dafa;
}

.TodoForm-Button--Container{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin-top: 14px;    

}
.TodoForm-Button{
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;


}.TodoForm-Button--Add{
    background-color: #61dafa;
    box-shadow: 0px 5px 25px rgba(97,218,250,0.5);

}
.TodoForm-Button--Cancel{
    background: transparent;

}